import { PublicodesExpression } from '..'
import { makeASTTransformer } from '../AST'
import { ASTNode, ConstantNode, EvaluatedNode } from '../AST/types'
import { PublicodesError } from '../error'
import parse from '../parse'
import { Context, createContext } from '../parsePublicodes'

export function createParseInlinedMecanism(
	name: string,
	args: Record<string, { 'par dfaut'?: PublicodesExpression; type?: 'liste' }>,
	body: PublicodesExpression
) {
	let parsedBody
	let parsedDefaultArgs
	function parseInlineMecanism(providedArgs, context) {
		parsedBody ??= parse(body, createContext({ dottedName: 'INLINE_MECANISM' }))
		parsedDefaultArgs ??= {}
		for (const name in args) {
			if ('par dfaut' in args[name]) {
				parsedDefaultArgs[name] = parse(
					args[name]['par dfaut'],
					createContext({})
				)
			}
		}

		// Case of unary mecanism
		if (Object.keys(args).length === 1 && 'valeur' in args) {
			providedArgs = {
				valeur: providedArgs,
			}
		}

		const parsedProvidedArgs = {}
		for (const name in providedArgs) {
			parsedProvidedArgs[name] = parse(providedArgs[name], context)
		}

		const parsedInlineMecanism = makeASTTransformer((node) => {
			if (node.nodeKind !== 'reference' || !(node.name in args)) {
				return
			}
			const argName = node.name
			if (argName in parsedProvidedArgs) {
				return parsedProvidedArgs[argName]
			}
			if (argName in parsedDefaultArgs) {
				return parsedDefaultArgs[argName]
			}
			throw new PublicodesError(
				'SyntaxError',
				`Il manque la cl '${argName} dans le mcanisme ${name}`,
				{ dottedName: argName }
			)
		})(parsedBody)

		parsedInlineMecanism.sourceMap = {
			mecanismName: name,
			args: parsedProvidedArgs,
		}

		return parsedInlineMecanism
	}

	parseInlineMecanism.nom = name

	return Object.assign(parseInlineMecanism, 'name', {
		value: `parse${toCamelCase(name)}Inline`,
	})
}

/**
 Note : Les transformations de mcanisme oprant sur les listes sont plus couteuses que celles oprant sur des scalaires.

 Cela vient du fait qu'il n'y a pas la possibilit de dfinir ces transformations dans publicodes : il manque le type liste et les oprations de bases associes (reduce, map).

 On doit donc dplier l'opration statiquement, au parsing, ce qui prend plus de temps, au parsing et  l'valuation. somme: [1,2,3] est transform en (1 + 2) + 3).

 De manire gnral, les baisse en performances de cette PR sont attenduee : il s'agit d'une contrepartie logique de l'utilisation de mcanisme de base publicodes. Ce qu'on gagne en solidit de l'valuation & en amlioration du typage, on le perd en performance. C'est logique puisque l'evaluation de ces mcanisme n'est plus du JS natif mais passe par une structure intermdiaire.

 Pour amliorer les perfs, il y a plusieurs pistes :

	- optimiser d'avantage les oprations de bases
	- ajouter les listes et les oprations sur les listes dans publicodes
	- ajouter une implmentation "native" de certains mcanismes utiliss (on gagne quand mme  les dcomposer en mcanismes de base pour la partie spcification et typage).
 */
export function createParseInlinedMecanismWithArray(
	name: string,
	args: Record<string, { type?: 'liste' }>,
	body: (args: Record<string, ASTNode | Array<ASTNode>>) => PublicodesExpression
) {
	function parseInlineMecanism(providedArgs, context: Context) {
		// Case of unary mecanism
		if (Object.keys(args).length === 1 && 'valeur' in args) {
			providedArgs = {
				valeur: providedArgs,
			}
		}

		const parsedProvidedArgs = {}
		for (const name in providedArgs) {
			const value = providedArgs[name]
			parsedProvidedArgs[name] = Array.isArray(value)
				? value.map((v) => parse(v, context))
				: parse(value, context)
		}

		const parsedInlineMecanism = parse(body(parsedProvidedArgs), context)
		parsedInlineMecanism.sourceMap = {
			mecanismName: name,
			args: parsedProvidedArgs,
		}
		return parsedInlineMecanism
	}

	parseInlineMecanism.nom = name

	return Object.assign(parseInlineMecanism, 'name', {
		value: `parse${toCamelCase(name)}Inline`,
	})
}

function toCamelCase(str: string) {
	return str
		.replace(/(?:^\w|[A-Z]|\b\w)/g, (ltr) => ltr.toUpperCase())
		.replace(/\s+/g, '')
}

export const notApplicableNode = {
	nodeKind: 'constant',
	nodeValue: null,
	missingVariables: {},
	type: undefined,
	isNullable: true,
} as ConstantNode & EvaluatedNode

export const undefinedNode = {
	nodeKind: 'constant',
	nodeValue: undefined,
	missingVariables: {},
	type: undefined,
	isNullable: false,
} as ConstantNode & EvaluatedNode

export const undefinedNumberNode = {
	...undefinedNode,
	type: 'number',
} as ConstantNode & EvaluatedNode
