import { EvaluationFunction } from '..'
import { ASTNode, Unit } from '../AST/types'
import { convertToDate, convertToString } from '../date'
import { registerEvaluationFunction } from '../evaluationFunctions'
import { defaultNode, mergeAllMissing, parseObject } from '../evaluationUtils'
import { parseUnit } from '../units'

export type DureNode = {
	explanation: {
		depuis: ASTNode
		"jusqu'": ASTNode
	}
	unit: Unit
	nodeKind: 'dure'
}

const todayString = convertToString(new Date())
const objectShape = {
	depuis: defaultNode(todayString),
	"jusqu'": defaultNode(todayString),
}
const evaluate: EvaluationFunction<'dure'> = function (node) {
	const from = this.evaluateNode(node.explanation.depuis)
	const to = this.evaluateNode(node.explanation["jusqu'"])
	let nodeValue
	if ([from, to].some(({ nodeValue }) => nodeValue === undefined)) {
		nodeValue = undefined
	} else {
		const [fromDate, toDate] = ([from.nodeValue, to.nodeValue] as string[]).map(
			convertToDate
		)
		nodeValue = Math.max(
			0,
			Math.round(
				(toDate.getTime() - fromDate.getTime()) / (1000 * 60 * 60 * 24)
			)
		)
	}
	return {
		...node,
		missingVariables: mergeAllMissing([from, to]),
		nodeValue,
		explanation: {
			depuis: from,
			"jusqu'": to,
		},
	}
}

export default (v, context) => {
	const explanation = parseObject(objectShape, v, context)
	return {
		explanation,
		unit: parseUnit('jour'),
		nodeKind: 'dure',
	} as DureNode
}

registerEvaluationFunction('dure', evaluate)
