import { EvaluationFunction } from '..'
import { ASTNode } from '../AST/types'
import { registerEvaluationFunction } from '../evaluationFunctions'
import parse from '../parse'
import { createParseInlinedMecanism } from './inlineMecanism'

export type EstNonDfiniNode = {
	explanation: ASTNode
	nodeKind: 'est non dfini'
}

export function parseEstNonDfini(v, context) {
	const explanation = parse(v, context)
	return {
		explanation,
		nodeKind: 'est non dfini',
	} as EstNonDfiniNode
}
parseEstNonDfini.nom = 'est non dfini'

const parseEstDfini = createParseInlinedMecanism(
	'est dfini',
	{
		valeur: {},
	},
	{
		'=': [{ 'est non dfini': 'valeur' }, 'non'],
	}
)

const parseEstApplicable = createParseInlinedMecanism(
	'est applicable',
	{
		valeur: {},
	},
	{
		'=': [{ 'est non applicable': 'valeur' }, 'non'],
	}
)

export { parseEstDfini, parseEstApplicable }

const evaluate: EvaluationFunction<'est non dfini'> = function (node) {
	const valeur = this.evaluateNode(node.explanation)
	let nodeValue: boolean | undefined | null = false
	if (valeur.nodeValue === undefined) {
		nodeValue = true
	}

	return {
		...node,
		nodeValue,
		missingVariables: valeur.missingVariables,
		explanation: valeur,
	}
}
registerEvaluationFunction('est non dfini', evaluate)
